import React from 'react';

const LinuxNetwork = (props) => {

  return (
    <div className="common_style">

      <div className='block1'>
        <details>
          <summary> httpd </summary>
          <div className='block2'>

            <h2 className='h2'>    </h2>
            <li> rpm -qa httpd <small> # httpd   </small> </li>
            <li> yum install -y httpd <small> # httpd  </small> </li>
            <li> systemctl status httpd, systemctl status httpd.service <small> # httpd    </small> </li>
            <li> apachectl configtest <small> # Syntax Error  </small> </li>
            <li>  </li>

          </div>
        </details>
      </div>

      <div className='block1'>
        <details>
          <summary> iptables  - <small> #   </small> </summary>
          <div className='block2'>

            <h2 className='h2'>   </h2>
            <li>        </li>
            <li> INPUT, FORWARD, OUTPUT :  ,  ,   </li>
            <li> SNAT DNAT : SNAT IP IP , DNAT IP IP  </li>
            <li> NEW(), ESTABLISHED(), INVALID(), RELATED() </li>
            <li> DROP, ACCEPT <small> #      </small> </li>

            <h2 className='h2'>   </h2>
            <li> rpm -qa | grep iptables <small> # iptables   </small> </li>
            <li> systemctl enable iptables </li>
            <li> systemctl start iptables </li>
            <li> systemctl status iptables </li>
            <li>  </li>
            <li> iptables -F <small> #   </small> </li>
            <li> iptables -N <small> #   </small> </li>
            <li> iptables -L <small> #   ,   </small> </li>
            <li> iptables -X <small> #    </small> </li>
            <li> iptables -P <small> #    </small> </li>
            <li> iptables -A [INPUT]<small> #      </small> </li>
            <li> iptables -I <small> #       </small> </li>
            <li> iptables -R <small> #    </small> </li>
            <li> iptables -D <small> #   </small> </li>
            <li> iptables -C <small> #   </small> </li>
            <li> iptables -s [IP] <small> #   </small> </li>
            <li> iptables -d [IP,] <small> #   </small> </li>
            <li> iptables -p [TCP, ] <small> #  </small> </li>
            <li> iptables -i <small> #     </small> </li>
            <li> iptables -o <small> #     </small> </li>
            <li> iptables -f <small> #  ? </small> </li>
            <li> iptables -j [DROP, ACCEPT]<small> #       </small> </li>
            <li> recent --update --seconds [] --hitcount [] --name HTTP
              <small> #     (    ) </small> </li>
            <li> --sport [] <small> #   </small> </li>
            <li> --dport [, :(??)] <small> #   </small> </li>
            <li> --tcp-flags [SYN,RST,ACK] [SYN,RST,ACK] <small> # TCP    </small> </li>
            <li> -m state --state [NEW, ESTABLISHED, INVALID RELATED] <small> #      </small> </li>
            <li>  </li> <br />

            <li> vi /etc/sysconfig/iptables
              <li> -A PREROUTING -i eth0 -p tcp -m tcp --dport [] -j DNAT --to-destination [IP] </li>
              <li>  </li>
            </li>
            <li> /etc/init.d/iptables restart </li>

            <h2 className="h2">   </h2>
            <li> iptables -t nat -A PREROUTING -p tcp --dport [1] -j REDIRECT --to-port [2] <small> # 1 2  </small> </li>
            <li> iptables -t nat -D PREROUTING -p tcp --dport [1] -j REDIRECT --to-port [2] <small> # 1 2     </small> </li>
            <li> iptables -I INPUT -p tcp -s 0.0.0.0/0 -d [IP] --dport [] -j ACCEPT <small> #   IP:     </small> </li>
            <li> iptables -I FORWARD -m tcp -p tcp -d [IP] --dport [] -j ACCEPT <small> #  IP:   </small> </li>
            <li> iptables -I FORWARD -m state -p tcp -d [IP/] --state NEW,RELATED,ESTABLISHED -j ACCEPT <small> # NEW,RELATED,ESTABLISHED      </small> </li>
            <li> iptables -t nat -I PREROUTING -p tcp -d [IP1] --dport [1] -j DNAT --to-destination [IP2:2] <small> # IP1:1   IP2:2  </small>   </li>
            <li> iptables -t nat -nL <small> #    </small> </li>
            <li> iptables -l INPUT 1 -p tcp --dport [] -j ACCEPT <small> # </small>  </li>
            <li> iptables --list </li>
            <li>  </li>

          </div>
        </details>
      </div>

    </div>
  );
}

export default LinuxNetwork;