import React from 'react';
import { Link } from 'react-router-dom';

const KeyMapList = () => {

  return (
    <div className="navMenuContainer2">
      <div className="menu menu1">
        <Link to="keymap_intellij"> Intellij ,  </Link>
        <Link to="keymap_vscode"> VScode ,  </Link>
        <Link to="keymap_eclipse"> Eclipse    </Link>
        <Link to="keymap_sublimetext"> SublimeText    </Link>
      </div>
      <div className="menu menu2">
        <Link to="keymap_window"> Window    </Link>
        <Link to="keymap_browser"> Browser    </Link>
      </div>
    </div>
  );
}

export default KeyMapList;