import React from 'react';
import styled, { keyframes } from 'styled-components';
import { Route, Routes, Link } from 'react-router-dom';
import TodoMain from './component/Pages/TodoMain';
import TodoCalender from './component/Pages/TodoCalender';
//import TodoStudy from './component/Pages/TodoStudy';
//import TodoStatistics from './component/Pages/TodoStatistics';
//import TodoList from './component/Pages/TodoList';
import Private from '../AuthContainer/Private';

// 440, 960, 1440
const Container = styled.div`
  min-width: 460px;
  background-clip: content-box;
  width: 100vw;
  min-height: 500px;
  height: 100%;
  padding: 10px;
  border-radius: 10px;

`;
const Container1 = styled.div`
  height: 100%;
  padding: 20px;
  border-radius: 10px;
  background-color: #C4C4C4;
`;
const Container2 = styled.div`
  display: flex;
  height: 100%;
  background-color: white;
  padding: 10px;
  border-radius: 10px;
  flex-flow: wrap row;
`;
const TitleContainer = styled.header`
  width: 100%;
  height: 50px;
  background-color: #C0B4FA;
  border-radius: 10px;
  margin-bottom: 10px;
  font-family: 'Cafe24Ssurround';
  `;
const Title = styled.h2`
  line-height: 50px;
  text-align: center;
`;
const SideContainer = styled.div`
  width: 40px;
  margin-right: 10px;
  height: calc(100% - 60px);
  text-align: center;
  line-height: 40px;
  font-family: 'GangwonEduPowerExtraBoldA';
  font-size: 1.8rem;
  position: relative;
  overflow: scroll;
  
`;
const LinkStyle = styled(Link)`
  display: block;
  width: 40px;
  height: 40px;
  background-color: #C0B4FA;
  border-radius: 10px;
  margin-bottom: 10px;
  line-height: 40px;
  text-align: center;
  
  &:hover {
    color: #C0B4FA;
    background: black;
  }
`;

const TodoContainer = () => {
  return (
    <Container>
      <Container1>
        <Container2>
          <TitleContainer> <Title>    </Title>  </TitleContainer>
          <Private>
            <SideContainer>
              <LinkStyle to="main">  </LinkStyle>
              <LinkStyle to="calender" >  </LinkStyle>
              <LinkStyle to="list">  </LinkStyle>
              <LinkStyle to="study">  </LinkStyle>
              <LinkStyle to="statistics" >  </LinkStyle>
            </SideContainer>
            <Routes>
              <Route path={"/main"} element={<TodoMain />} />
              <Route path={"/calender"} element={<TodoCalender />} />
              {/*<Route path={"/list"} element={<TodoList />} />
              <Route path={"/study"} element={<TodoStudy />} />
              <Route path={"/statistics"} element={<TodoStatistics />} />*/}
            </Routes>
          </Private>
        </Container2>
      </Container1>
    </Container>
  );
};

export default TodoContainer;