import React from 'react';

const LinuxAccountCommand = (props) => {
    return (
        <div className="common_style">

            <div className='block1'>
                <details>
                    <summary> passwd <small> #    </small> </summary>
                    <div className='block2'>

                        <h2 className='h2'>   </h2>
                        <li> passwd [] <small> #    </small> </li>
                        <li> vi /etc/passwd <small> #     </small>
                            <div className="block4">
                                <li> []:[]:[UID]:[GID]:[comment]:[]:[] </li>
                                <li>  x /etc/shadow     </li>
                                <li>   /bin/bash </li>
                            </div>
                        </li>

                    </div>
                </details>
            </div>

            <div className='block1'>
                <details>
                    <summary> adduser, useradd <small> #   </small> </summary>
                    <div className='block2'>

                        <h2 className='h2'>   </h2>
                        <li> [adduser, useradd] [] <small> #   </small> </li>
                        <li> [adduser, useradd] -u [UID] <small> #  UID  </small> </li>
                        <li> [adduser, useradd] -g [GID] <small> #  GID  </small> </li>
                        <li> [adduser, useradd] -d [/] <small> #    </small> </li>
                        <li> [adduser, useradd] -m -d [/] <small> #     </small> </li>
                        <li> [adduser, useradd] -s [/] <small> #     </small> </li>
                        <li> [adduser, useradd] -c [] [] <small> #    </small> </li>

                    </div>
                </details>
            </div>

            <div className='block1'>
                <details>
                    <summary> id <small> #     </small> </summary>
                    <div className='block2'>

                        <h2 className='h2'>   </h2>
                        <li> id <small> # </small> </li>
                        <li> cat /etc/passwd <small> # </small> </li>


                    </div>
                </details>
            </div>

            <div className='block1'>
                <details>
                    <summary> su sudo <small> #     </small> </summary>
                    <div className='block2'>

                        <h2 className='h2'>   </h2>
                        <li> su <small> # root   </small> </li>
                        <li> su [] <small> #    </small> </li>

                        <h2 className='h2'>  root  sudo     </h2>
                        <div className="block4">
                            <li> su root  </li>
                            <li> visudo -f /etc/sudoers </li>
                            <li> 100  root ALL=(ALL) ALL  </li>
                            <li> [] ALL=(ALL) ALL  </li>
                            <li>   sudo    </li>
                        </div>
                        <div className="block4">
                            <li>    . </li>
                            <li> su root  </li>
                            <li> cd /etc </li>
                            <li> chmod u+w sudoers </li>
                            <li> vi /etc/sudoers  </li>
                            <li> 100  root ALL=(ALL) ALL  </li>
                            <li> [] ALL=(ALL) ALL  </li>
                            <li> chmod u-w sudoers </li>
                            <li>   sudo    </li>
                        </div>

                    </div>
                </details>
            </div>

            <div className='block1'>
                <details>
                    <summary> change <small> #        </small> </summary>
                    <div className='block2'>

                        <h2 className='h2'>   </h2>
                        <li> change -l [] <small> #       </small> </li>
                        <li> change -E YYYY/MM/DD [] <small> #      </small> </li>
                        <li> change -m [DAY] [] <small> #      ?,  .. </small> </li>
                        <li> change -M [DAY] [] <small> #        </small> </li>
                        <li> change -W [DAY] [] <small> #     [DAY]  . </small> </li>
                        <li> change -I [DAY] [] <small> #     [DAY]   . </small> </li>

                    </div>
                </details>
            </div>

            <div className='block1'>
                <details>
                    <summary> usermod <small> #    </small> </summary>
                    <div className='block2'>

                        <h2 className='h2'>   </h2>
                        <li> usermod [] -c [] <small> #   </small> </li>
                        <li> usermod -d [/] [] <small> #    </small> </li>
                        <li> usermod -s [] [] <small> #  </small> </li>
                        <li> usermod -c [] [] <small> #    </small> </li>
                        <li> usermod -e [YYYY-MM-DD] [] <small> #      </small> </li>
                        <li> usermod -g [] [] <small> #    </small> </li>
                        <li> usermod -G [] [] <small> # </small> </li>
                        <li> usermod -a -G [] [] <small> #    </small> </li>

                    </div>
                </details>
            </div>

            <div className='block1'>
                <details>
                    <summary> userdel <small> #   </small> </summary>
                    <div className='block2'>

                        <h2 className='h2'>   </h2>
                        <li> userdel [] </li>
                        <li> userdel -r [] <small> #      </small> </li>



                    </div>
                </details>
            </div>

            <div className='block1'>
                <details>
                    <summary> gpasswd <small> #  ,, </small> </summary>
                    <div className='block2'>

                        <h2 className='h2'>   </h2>
                        <li> gpasswd [] <small> #   . </small> </li>
                        <li> gpasswd -a [] [] <small> #   . </small> </li>
                        <li> gpasswd -A [] [] <small> #     </small> </li>
                        <li> gpasswd -d [] [] <small> #    </small> </li>
                        <li> gpasswd -r <small> #     </small> </li>
                        <li> gpasswd -R [] <small> #    </small> </li>
                        <li> gpasswd -M [] [] <small> #      </small> </li>

                    </div>
                </details>
            </div>

            <div className='block1'>
                <details>
                    <summary> groupadd <small> #   </small> </summary>
                    <div className='block2'>

                        <h2 className='h2'>   </h2>
                        <li> groupadd [] <small> #   </small> </li>
                        <li> groupadd -g [GID] [] <small> #  GID  ,  GID   </small> </li>

                    </div>
                </details>
            </div>

            <div className='block1'>
                <details>
                    <summary> groupmod <small> #    </small> </summary>
                    <div className='block2'>

                        <h2 className='h2'>   </h2>
                        <li> groupmod -g [GID] [GID] <small> # GID  </small> </li>
                        <li> groupmod -n [] [] <small> #   </small> </li>
                        <li> groupmod -o <small> #  GID ,    </small> </li>
                        <li> vi /etc/group  <small> #     </small> </li>

                    </div>
                </details>
            </div>

            <div className='block1'>
                <details>
                    <summary> groupmems <small> #    </small> </summary>
                    <div className='block2'>

                        <h2 className='h2'>   </h2>
                        <li> groupmems -g [] -l <small> #    </small> </li>
                        <li> groupmems -g [] -a [] <small> #    </small> </li>
                        <li> groupmems -g [] -d [] <small> #    </small> </li>
                        <li> vi /etc/group <small> #     </small>
                            <div className="block4">
                                <li> []:x:[GID]: <small> #   </small> </li>
                            </div>
                        </li>

                    </div>
                </details>
            </div>

            <div className='block1'>
                <details>
                    <summary> groupdel <small> #   </small> </summary>
                    <div className='block2'>

                        <h2 className='h2'>   </h2>
                        <li> groupdel [] <small> #   </small> </li>

                    </div>
                </details>
            </div>

            <div className='block1'>
                <details>
                    <summary> groups <small> #      </small> </summary>
                    <div className='block2'>

                        <h2 className='h2'>   </h2>
                        <li> groups <small> #      </small> </li>
                        <li> cat /etc/group <small> #      </small> </li>

                    </div>
                </details>
            </div>

            <div className='block1'>
                <details>
                    <summary> chmod  <small> #    </small> </summary>
                    <div className='block2'>

                        <h2 className='h2'>  chmod [] [] [] </h2>
                        <small>  = #(u)/(g)/(o),(a) 777 = r(4),w(2),x(1) , t(Sticky bit) , X() , s(SUID) </small>
                        <li> chmod (755)  ,   . </li>
                        <li> chmod ugo+rwx  , # user,group,other  r(),w(),x()  .  </li>
                        <li> chmod ugo=rwx  , # user,group,other  r(),w(),x()    </li>
                        <li> chmod ugo-rwx  , # user,group,other  r(),w(),x()  .  </li>
                        <li> chmod ug=w,o=r  , # user,group,other  r(),w(),x()  .  </li>
                        <li> chmod (775) * , #       </li>
                        <li> chmod (1775) * , #       ,   (Sticky bit)  </li>
                        <li> chmod (4775) * , #       ,
                            SUID, {' -> 1 ()'} | {' -> 2()'} | {'->1->2( )'}         . </li>
                        <li> chmod -R   , #        </li>
                        <li> chmod -c   :         </li>
                        <li> chmod -f   :     </li>
                        <li> chmod -v   :         </li>

                    </div>
                </details>
            </div>

            <div className='block1'>
                <details>
                    <summary> chown  <small> # ,   </small> </summary>
                    <div className='block2'>

                        <h2 className='h2'>   </h2>
                        <li> chown [] [] , #     </li>
                        <li> chown []:[]  , #  ,   </li>
                        <li> chown -R []:[] [] , #   </li>
                        <li> chown -h [] [] , #      </li>

                    </div>
                </details>
            </div>

        </div>
    );
}

export default LinuxAccountCommand;